.app {
  text-align: center;
  border: 1px solid red;
  display: flex;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.controls{
  flex: 0;
}

.window {
  flex: 1
}

.box{
  height: 100%;
  width: 300px;
  padding: 0 20px ;
  background-image: url('./images/arc-plan.jpg');
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
  position: relative;
  cursor: pointer;
}

.box_nav_icon {
    position: absolute;
    right: -50px;
    width: 50px;
    height: 50px;
    background-color: #fff;
    background-image: url('./images/drag.svg');
    background-position: center;
    background-size: 25px;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
}